import React from 'react';

// import img
import discord from '../../assets/imgs/discord.png'
import projectoi from '../../assets/imgs/projectoi.png'
import tesla from '../../assets/imgs/Screenshot_tesla.png'
import kick from '../../assets/imgs/kickapp.png'
import easybank from '../../assets/imgs/easybank.jpg'
import thomas from '../../assets/imgs/thomas.jpg'

// import css
import './projects.css';
import './projects-responsive.css';

const Projects = () => {
    return (
        <div className="projectsContainer">
            <div id="projects"/>
            <div className="projectsContent">
                <h2 className="projects">My Recent Work</h2>
                <h3 className="desc">Here are a few projects I've worked on recently.</h3>
                <div className="grid-2">
                    <a href="https://kickapp.fr">
                        <img className="imgdiv" src={kick} alt="img"/>
                        <div className="info">
                            <h1>Kick App - Web Designer</h1>
                            <p>Kick is a phone app based on the Ionic framework and Angular. I was hired to to design the website using Illustrator, Xd and Photoshop.</p>
                        </div>
                    </a>
                    <a href="https://dribbble.com/shots/13953856-Dashboard-and-Web-hosting-page-Projec-toi">
                        <img className="imgdiv" src={projectoi} alt="img"/>
                        <div className="info">
                            <h1>Projec'toi - Web Designer</h1>
                            <p>Projec'toi is a company that connects freelancers with clients. My role in this project was to design a few pages using Photoshop and Xd.</p>
                        </div>
                    </a>
                    <a href="https://discordinvites.net">
                        <img className="imgdiv" src={discord} alt="img"/>
                        <div className="info">
                            <h1>DiscordInvites - Front End</h1>
                            <p>DiscordInvites is a discord server advertising website. I assisted the team on the app using jQuery, HTML and CSS. The app source code is private.</p>
                        </div>
                    </a>
                    <a href="https://ednese.github.io/portfolio-thomas-vue">
                        <img className="imgdiv" src={thomas} alt="img"/>
                        <div className="info">
                            <h1>Thomas - Front End</h1>
                            <p>This site is the portfolio of Thomas Jauze. I was hired to integrate the graphic model using VueJs and natives web technos.</p>
                        </div>
                    </a>
                    <a href="https://github.com/ednese/easybank">
                        <img className="imgdiv" src={easybank} alt="img"/>
                        <div className="info">
                            <h1>Easybank - Front End</h1>
                            <p>Easybank is a site whose layout and elements are provided by Frontend Mentor. I coded it under React with html, css and js to create this site.</p>
                        </div>
                    </a>
                    <a href="https://github.com/ednese/tesla">
                        <img className="imgdiv" src={tesla} alt="img"/>
                        <div className="info">
                            <h1>Tesla - Front End</h1>
                            <p>Tesla is an famous autonomous car company. The goal of this project was to rebuild the first page of their site to familiarize myself with vuejs.</p>
                        </div>
                    </a>
                </div>
            </div>
        </div>
    )
}

export default Projects;